<template>
    <div class="line-select">
        <button @click="cancelSelect">取消</button>
        <div class="line-select-wrap">
            <div class="option" v-for="token of tokens" :key="token.lineUid">
                <p class="hint">{{ token.hint }}</p>
                <button class="delete-button" @click="select(token.lineUid)">删除</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "LineSelect",
        props: ['tokens'],
        methods: {
            select(lineUid) {
                this.$emit('delete-line', lineUid);
            },
            cancelSelect() {
                this.$emit('cancel-select');
            }
        },
    }
</script>

<style scoped>

    .line-select {
        position: fixed;
        width: 100%;
        top: 2em;
        text-align: center;
    }

    .line-select-wrap {
        margin: auto;
        width: 50%;
    }

    .option {
        display: flex;
        margin-top: .2em;
        border: #42b983 solid .1em;
        border-radius: 1em;
        overflow: hidden;
        box-sizing: border-box;
    }

    .hint {
        flex: 1;
        margin: .2em 1em;
        box-sizing: border-box;
    }

    .delete-button {
        padding: .2em 1em;
        font-size: .8em;
        box-sizing: border-box;
    }


</style>