<template>
    <div class="sl-view">
        <div class="sl-wrap">
            <textarea class="data" v-model="data"/>
            <div class="button-bar">
                <button @click="save">导出</button>
                <button @click="close">关闭</button>
                <button @click="load">载入</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SLView",
        props: ['pool'],
        data() {
            return {
                data: '',
            }
        },
        methods: {
            load() {
                let json = this.data;
                let pool = JSON.parse(json);
                if (pool !== null && pool.nodes && pool.lines) {
                    this.$emit("load", pool);
                }
            },
            save() {
                this.data = JSON.stringify(this.pool);
            },
            close() {
                this.$emit("sl-close");
            }
        },
    }
</script>

<style scoped>

    .sl-view {
        position: fixed;
        top: 2em;
        bottom: 2em;
        left: 2em;
        right: 2em;

    }

    .sl-wrap {
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;

        display: flex;
        flex-direction: column;

        border: #42b983 solid .2em;
        border-radius: 1em;

        background: #FFFFFFEE;

        box-sizing: border-box;
        padding: 1em;
    }

    .data {
        flex: 1;
        outline: none;
        border-radius: .5em;
        font-size: 1em;
        font-family: Consolas, sans-serif;
        resize: none;
        padding: 1em;
    }
</style>