<template>
    <div ref="node" class="mind-node"
         @mouseenter="dragging = false"
         @dragover="$event.preventDefault()"
         @drop="lineEnd($event)"
         @mousemove="dragNode($event)"
         @mouseup="dragNodeEnd($event)"
    >
        <div class="top-bar">
            <div class="move-handle"
                 @mousedown="dragNodeStart($event)"
            >移动</div>
            <div class="line-handle"
                 draggable="true"
                 @dragstart="lineStart($event)"
            >链接</div>
        </div>
        <input v-model="node.content" />
        <div class="button-bar">
            <button @click="removeLine">删除链接</button>
            <button @click="deleteNode">删除节点</button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "MindNode",
        props: ['node'],
        data() {
            return {
                dragging: false,
                prevX: 0,
                prevY: 0,
            }
        },
        methods: {
            dragNodeStart(event) {
                if (event.button === 0) {
                    this.dragging = true;
                    this.prevX = event.screenX;
                    this.prevY = event.screenY;
                }
            },
            dragNode(event) {
                if (this.dragging) {
                    let dx = event.screenX - this.prevX;
                    let dy = event.screenY - this.prevY;
                    this.prevX = event.screenX;
                    this.prevY = event.screenY;

                    let node = this.$refs.node
                    this.node.posX += dx;
                    this.node.posY += dy;

                    let style = node.style;
                    style.left = (this.node.posX - node.clientWidth / 2) + 'px';
                    style.top = (this.node.posY - node.clientHeight / 2) + 'px';

                    event.preventDefault();
                    this.moveNode();
                }
            },
            dragNodeEnd(event) {
                if (event.button === 0) {
                    this.dragging = false;
                    this.prevX = 0;
                    this.prevY = 0;
                }
            },

            lineStart(event) {
                event.dataTransfer.setData("text", String(this.node.uid));
                event.dataTransfer.dropEffect = "link";
            },
            lineEnd(event) {
                let srcUid = Number(event.dataTransfer.getData("text"));
                let dstUid = this.node.uid;
                event.preventDefault();
                if (srcUid !== dstUid) {
                    this.$emit("create-line", srcUid, dstUid);
                }
            },

            moveNode() {
                this.$emit("move-node", this.node.uid);
            },

            deleteNode() {
                this.$emit("delete-node", this.node.uid);
            },

            removeLine() {
                this.$emit("remove-line", this.node.uid);
            },

        },
        mounted() {
            let node = this.$refs.node;
            let style = node.style;
            style.left = (this.node.posX - node.clientWidth / 2) + 'px';
            style.top = (this.node.posY - node.clientHeight / 2) + 'px';
        }
    }
</script>

<style scoped>

    .mind-node {
        position: absolute;
        padding: .5em;

        user-select: none;

        background: #FFFFFF;
        box-shadow: darkgrey 2px 2px 2px;

        border: #ffa5af solid .1em;
        border-radius: .5em;
    }

    .top-bar {
        display: flex;
        padding-bottom: .2em;
        margin-bottom: .2em;
        border-bottom: darkgrey solid .05em;
    }

    .move-handle {
        flex: 1;
        display: inline;
        padding: .2em .5em;
        box-sizing: border-box;

        color: darkgrey;
        font-size: .6em;

        border: #a8e4ff solid .1em;
        border-top-left-radius: .5em;
    }

    .line-handle {
        flex: 1;
        display: inline;
        padding: .2em .5em;
        box-sizing: border-box;

        color: darkgrey;
        font-size: .6em;

        border: #ffb9ba solid .1em;
        border-top-right-radius: .5em;
    }

    .button-bar {
        font-size: .5em;
    }
</style>