<template>
    <div ref="pool" class="mind-node-pool"
         @mousedown="dragViewStart($event)"
         @mousemove="dragView($event)"
         @mouseup="dragViewEnd($event)"
    >
        <canvas class="line-canvas" ref="lineCanvas"></canvas>
        <div class="anchor" ref="anchor">
            <div class="mind-node-wrap">
                <MindNode v-for="node of nodes" :key="node.uid" :node="node"
                          @create-line="appendLine"
                          @move-node="drawLines"
                          @delete-node="deleteNode"
                          @remove-line="removeLineOfNode"
                />
            </div>
        </div>
        <SLView v-if="slOpened" ref="sl" :pool="pool" @sl-close="closeSL" @load="load"/>
        <div v-if="!slOpened" class="open" @click="slOpened = true">
            <p>SL</p>
        </div>
        <LineSelect v-if="lineSelectOfNode >= 0" :tokens="lineSelectTokens"
                    @delete-line="deleteLine"
                    @cancel-select="lineSelectOfNode = -1"
        />
    </div>
</template>

<script>
    import MindNode from "@/components/MindNode";
    import {createLine, createNode, getIG, setIG} from "@/mind-node";
    import SLView from "@/components/SLView";
    import LineSelect from "@/components/LineSelect";
    export default {
        name: "MindNodePool",
        components: {LineSelect, SLView, MindNode},
        data() {
            return {
                dragging: false,
                anchorX: 0,
                anchorY: 0,
                prevX: 0,
                prevY: 0,
                nodes: [],
                lines: [],
                slOpened: false,
                lineSelectOfNode: -1,
            };
        },
        computed: {
            pool() {
                return {
                    nodes: this.nodes,
                    lines: this.lines,
                    uidGenerator: getIG(),
                };
            },
            lineSelectTokens() {
                let node = this.nodes.find(n => n.uid === this.lineSelectOfNode);
                let tokens = node.outLines.reduce((prev, lineUid) => {
                    let line = this.lines.find(l => l.uid === lineUid);
                    if (line) {
                        let dst = this.nodes.find(n => n.uid === line.dst);
                        if (dst) {
                            prev.push({
                                lineUid,
                                hint: dst.content,
                            });
                        }
                    }
                    return prev;
                }, []);
                return tokens;
            }
        },
        methods: {
            dragViewStart(event) {
                if (event.button === 1) {
                    this.dragging = true;
                    this.prevX = event.screenX;
                    this.prevY = event.screenY;
                }
            },
            dragView(event) {
                if (this.dragging) {
                    let dx = event.screenX - this.prevX;
                    let dy = event.screenY - this.prevY;
                    this.prevX = event.screenX;
                    this.prevY = event.screenY;

                    let node = this.$refs.anchor;
                    this.anchorX += dx;
                    this.anchorY += dy;

                    let style = node.style;
                    style.left = this.anchorX + 'px';
                    style.top = this.anchorY + 'px';

                    event.preventDefault();
                    this.drawLines();
                }
            },
            dragViewEnd(event) {
                if (event.button === 1) {
                    this.dragging = false;
                    this.prevX = 0;
                    this.prevY = 0;
                } else if (event.button == 2) {
                    this.addNode(event);
                    event.preventDefault();
                }
            },

            drawLines() {
                let canvas = this.$refs.lineCanvas;
                let ctx = canvas.getContext('2d');
                ctx.clearRect(0, 0, canvas.width, canvas.height);
                ctx.strokeStyle = "black";
                for (let line of this.lines) {
                    let src = this.nodes.find(node => node.uid === line.src);
                    let dst = this.nodes.find(node => node.uid === line.dst);
                    ctx.beginPath();
                    ctx.moveTo(this.anchorX + src.posX + 80, this.anchorY + src.posY);
                    ctx.lineTo(this.anchorX + dst.posX - 80, this.anchorY + dst.posY);
                    ctx.stroke();
                }
            },

            appendLine(srcUid, dstUid) {
                let line = createLine(srcUid, dstUid);
                this.lines.push(line);
                let src = this.nodes.find(node => node.uid === line.src);
                let dst = this.nodes.find(node => node.uid === line.dst);
                src.outLines.push(line.uid);
                dst.inLines.push(line.uid);
                this.drawLines();
            },

            addNode(event) {
                let node = createNode({
                    posX: event.offsetX - this.anchorX,
                    posY: event.offsetY - this.anchorY,
                });
                this.nodes.push(node);
            },

            deleteNode(nodeUid) {
                let index = this.nodes.findIndex(n => n.uid === nodeUid);
                let node = this.nodes[index];
                for (let lineUid of node.inLines) {
                    this.deleteLine(lineUid);
                }
                for (let lineUid of node.outLines) {
                    this.deleteLine(lineUid);
                }
                this.nodes.splice(index, 1);
                this.drawLines();
            },

            deleteLine(lineUid) {
                let index = this.lines.findIndex(n => n.uid === lineUid);
                if (index >= 0) {
                    let line = this.lines[index];
                    let src = this.nodes.find(node => node.uid === line.src);
                    let dst = this.nodes.find(node => node.uid === line.dst);
                    if (src) {
                        let i = src.outLines.indexOf(lineUid);
                        if (i >= 0) {
                            src.outLines.splice(i, 1);
                        }
                    }
                    if (dst) {
                        let i = dst.inLines.indexOf(lineUid);
                        if (i >= 0) {
                            dst.inLines.splice(i, 1);
                        }
                    }
                    this.lines.splice(index, 1);
                }
                this.drawLines();
            },

            load(pool) {
                this.nodes.splice(0, this.nodes.length, ...pool.nodes);
                this.lines.splice(0, this.lines.length, ...pool.lines);
                setIG(pool.uidGenerator);
                this.closeSL();
                this.drawLines();
            },

            closeSL() {
                this.slOpened = false;
            },

            removeLineOfNode(nodeUid) {
                this.lineSelectOfNode = nodeUid;
            },


        },
        mounted() {
            let anchor = this.$refs.anchor;
            this.anchorX = anchor.offsetLeft;
            this.anchorY = anchor.offsetTop;

            this.$refs.lineCanvas.width = this.$refs.pool.clientWidth;
            this.$refs.lineCanvas.height = this.$refs.pool.clientHeight;
        },
    }
</script>

<style scoped>
    .mind-node-pool {
        position: static;

        width: 100%;
        height: 100%;
        box-sizing: border-box;

        border: #cc8888 solid .2em;
        border-radius: 1em;

        overflow: hidden;
    }

    .anchor {
        position: relative;
        display: inline;
        top: 50%;
        left: 50%;

        background: #42b983;
    }

    .mind-node-wrap {
        position: absolute;
    }

    .line-canvas {
        position: absolute;
        top: 0;
        left: 0;

        z-index: -1;
    }

    .open {
        width: 3em;
        height: 3em;
        border: #42b983 solid .2em;
        box-sizing: border-box;
        border-radius: 1.5em;
        position: fixed;
        top: 1em;
        left: 1em;
        text-align: center;
        color: #42b983;
        background: #FFFFFF;
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        user-select: none;
    }
    .open > * {
        font-size: 1.5em;
        font-weight: bold;
        margin: auto;
        transform: translateY(-50%);
        position: relative;
        top: 50%;
    }
</style>