

let uidGenerator = 100;

function createNode({content = '[waiting input]', posX = 0, posY = 0}) {

    return {
        uid: uidGenerator++,
        content,
        posX,
        posY,
        inLines: [],
        outLines: [],
    }
}

function createLine(srcUid, dstUid) {
    return {
        uid: uidGenerator++,
        src: srcUid,
        dst: dstUid,
    }
}

function setIG(ig) {
    uidGenerator = ig;
}

function getIG() {
    return uidGenerator;
}

export {
    createNode,
    createLine,
    setIG,
    getIG,
};